import React, { useMemo } from "react"
import { formatDaysToText, formatTimestampToTime } from "@rp/react/common/brief"
import { BriefSchedule } from "@rp/react/tanstack-api/brief"
import { Box, Tooltip, Typography } from "@rp/react/web/mui/core"
import { AccessTime } from "@rp/react/web/mui/icons"

type BriefConfigScheduleInfoProps = {
  schedule: BriefSchedule
}

const BriefConfigScheduleInfo = ({ schedule }: BriefConfigScheduleInfoProps) => {
  const { daysText, timeText, fullTimeText } = useMemo(() => {
    const timeText = formatTimestampToTime(schedule.hour, schedule.minute)
    return {
      daysText: formatDaysToText(schedule.days),
      timeText,
      fullTimeText: `${timeText}, ${schedule.timezone}`,
    }
  }, [schedule])

  return (
    <Box
      display="flex"
      alignItems="center"
      role="time"
      aria-label="brief config schedule info"
      sx={{
        textOverflow: "ellipsis",
        overflow: "hidden",
        whiteSpace: "nowrap",
      }}
    >
      <AccessTime
        sx={{
          color: "BDprimary.200",
          height: "20px",
          width: "20px",
          marginTop: "-2px",
        }}
      />

      <Tooltip title={daysText}>
        <Typography noWrap color="BDprimary.200" marginLeft="4px" fontSize="14px">
          {daysText}
        </Typography>
      </Tooltip>

      <Typography color="BDprimary.200" marginX="4px" fontSize="14px">
        |
      </Typography>

      <Tooltip title={fullTimeText}>
        <Typography color="BDprimary.200" fontSize="14px" display="block" width="62px">
          {timeText}
        </Typography>
      </Tooltip>
    </Box>
  )
}

export { BriefConfigScheduleInfo }
